.read-btn{
    background-color: white;
    border: 0;
    color: rgb(69, 172, 206);
    cursor: pointer;
}
.btn{
    background-color: #fff;
    color: red;
    padding: 10px 50px;
    font-size: 22px;
    font-weight: bold;
    cursor: pointer;
    border: 1px solid brown;
    border-radius: 6px;
    margin: 20px auto 0;
    text-align: center;
}
img{
    width: 550px;
    height: 350px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
footer{
    padding: 30px;
}
.single-tour{
    background-color: #fff;
    box-shadow: 3px 8px 8px gray;
    border-radius: 10px;
    margin-bottom: 50px;
}
.name{
    float: left;
}
.price{
    color: rgb(69, 172, 206);
    float: right;
}
.clear-both{
    clear: both;
}
.tour-info{
    text-align: center;
}